.basicLightbox {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.8);
  opacity: 0.01;
  transition: opacity 0.4s ease;
  z-index: 1000;
  will-change: opacity;
}
.basicLightbox--visible {
  opacity: 1;
}
.basicLightbox__placeholder {
  max-width: 100%;
  transform: scale(0.9);
  transition: transform 0.4s ease;
  z-index: 1;
  will-change: transform;
}
.basicLightbox__placeholder > img:first-child:last-child,
.basicLightbox__placeholder > video:first-child:last-child,
.basicLightbox__placeholder > iframe:first-child:last-child {
  display: block;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  max-width: 95%;
  max-height: 95%;
}
.basicLightbox__placeholder > video:first-child:last-child,
.basicLightbox__placeholder > iframe:first-child:last-child {
  pointer-events: auto;
}
.basicLightbox__placeholder > img:first-child:last-child,
.basicLightbox__placeholder > video:first-child:last-child {
  width: auto;
  height: auto;
}
.basicLightbox--img .basicLightbox__placeholder, .basicLightbox--video .basicLightbox__placeholder, .basicLightbox--iframe .basicLightbox__placeholder {
  width: 100%;
  height: 100%;
  pointer-events: none;
}
.basicLightbox--visible .basicLightbox__placeholder {
  transform: scale(1);
}

.header {
  padding: 5rem;
  color: white;
  font-family: "Andika", sans-serif;
  position: relative;
  z-index: 10;
  height: 70rem;
}
@media (min-width: 768px) {
  .header {
    background-position: 50%;
  }
}
.header__imagen {
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url(/img/picomontana.avif);
  background-size: cover;
  background-size: auto 100%;
  background-position: center;
  background-position-y: 0px;
  background-position-x: 0px -660px;
  background-size: auto 120%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-repeat: no-repeat;
  z-index: -1;
}
@media (min-width: 768px) {
  .header__imagen {
    background-position-x: unset;
  }
}
@media (min-width: 1400px) {
  .header__imagen {
    background-size: 100%;
  }
}
.header__contenido {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
}
.header__heading {
  font-size: 4.2rem;
  text-align: center;
}
@media (min-width: 768px) {
  .header__heading {
    font-size: 6rem;
  }
}
@media (min-width: 1024px) {
  .header__heading {
    margin-top: 20rem;
  }
}
.header__texto {
  text-align: center;
}
.header__translate {
  text-decoration: none;
  background-color: transparent;
  border: none;
  color: #d0d2d0;
  font-size: 1.2rem;
}
.header__translate:hover {
  background-color: rgba(0, 0, 0, 0.2);
  color: white;
}

@media (max-width: 480px) {
  .nav {
    opacity: 0;
    visibility: hidden;
    position: absolute;
    background-color: rgba(0, 0, 0, 0.9);
    top: 0;
    right: 0;
    bottom: 0;
    padding: 0 2rem;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    gap: 5rem;
    box-shadow: 0 0 0 100vmax rgba(0, 0, 0, 0.9);
    font-size: 2.8rem;
  }
}
.nav__contenedor {
  width: 100%;
}
@media (min-width: 480px) {
  .nav__contenedor {
    width: 100%;
    padding: 1rem 0;
  }
}
.nav__contenedor.offset {
  transform: translateY(-100%);
  transition: all 0.5s;
}
.nav__contenedor.scrolling {
  transform: translateY(0);
}
.nav__contenedor.sticky {
  opacity: 0;
  visibility: hidden;
  background-color: #737773;
  background-image: linear-gradient(0deg, #737773 0%, #141914 100%);
  position: fixed;
  top: 0;
  left: 0;
}
.nav__contenedor.sticky.scrolling {
  opacity: 1;
  visibility: visible;
}
.nav__link {
  color: #d0d2d0;
  margin-bottom: 1rem;
  position: relative;
}
.nav__link::after {
  content: "";
  background: none repeat scroll 0 0 transparent;
  bottom: 0;
  display: block;
  height: 2px;
  left: 50%;
  position: absolute;
  background: #d0d2d0;
  transition: width 0.3s ease 0s, left 0.3s ease 0s;
  width: 0;
}
.nav__link:hover:after {
  width: 100%;
  left: 0;
}

.nav.visible {
  opacity: 1;
  visibility: visible;
}

.nav-open-icon,
.nav-close-icon {
  display: block;
  border: none;
  border-radius: 1rem;
  padding: 0.5rem;
  background-color: rgba(0, 0, 0, 0.7);
  color: #d0d2d0;
  font-size: 4.5rem;
  margin-top: 1rem;
}
@media (min-width: 480px) {
  .nav-open-icon,
  .nav-close-icon {
    display: none;
  }
}

.nav-open-icon {
  position: absolute;
  right: 4%;
  top: 0;
}

.navbar {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 5rem;
}
@media (min-width: 480px) {
  .navbar {
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
    gap: 2rem;
  }
}

.nav-sociales {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  gap: 2rem;
}
.nav-sociales__icon {
  font-size: 4rem;
  color: white;
  transition-property: transform;
  transition-duration: 0.3s;
}
.nav-sociales__icon:hover {
  transform: scale(1.1);
  color: #b3b3b3;
}

