.educacion__cv {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 2rem;
  margin: 5rem auto;
  z-index: 1;
}
.educacion__cv-link {
  z-index: 100;
  position: relative;
  font-size: inherit;
  font-family: inherit;
  color: #141914;
  padding: 0.5em 1em;
  outline: none;
  border: 1px solid #141914;
  background-color: white;
  overflow: hidden;
  transition: color 0.4s ease-in-out;
}
.educacion__cv-link::before {
  content: "";
  z-index: -1;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 1em;
  height: 1em;
  border-radius: 50%;
  background-color: #141914;
  transform-origin: center;
  transform: translate3d(-50%, -50%, 0) scale3d(0, 0, 0);
  transition: transform 0.45s ease-in-out;
}
.educacion__cv-link:hover {
  cursor: pointer;
  color: white;
}
.educacion__cv-link:hover::before {
  transform: translate3d(-50%, -50%, 0) scale3d(15, 15, 15);
}

