/* ------------------------------------------------------------------- 
 * ## portfolio list
 * ------------------------------------------------------------------- */
.folio-list {
  max-width: 1180px;
  margin-top: 5rem;
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .folio-list {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .folio-list {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.folio-item {
  height: 13rem;
  overflow: hidden;
  box-shadow: 0px 0px 2.6rem -0.8rem #141914;
  border-radius: 1rem;
}
.folio-item:nth-child(5) {
  order: -13;
}
.folio-item:nth-child(10) {
  order: -12;
}
.folio-item:nth-child(13) {
  order: -11;
}
.folio-item:nth-child(9) {
  order: -10;
}
.folio-item:nth-child(12) {
  order: -9;
}
.folio-item:nth-child(1) {
  order: -8;
}
.folio-item:nth-child(4) {
  order: 10;
}
.folio-item__thumb {
  display: block;
  background-color: white;
  position: relative;
  overflow: hidden;
}
.folio-item__thumb::before {
  z-index: 1;
  content: "";
  display: block;
  background-color: rgba(0, 0, 0, 0.8);
  opacity: 0;
  visibility: hidden;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  -webkit-transition: all, 0.5s;
  transition: all, 0.5s;
}
.folio-item__thumb::after {
  z-index: 1;
  content: "...";
  font-weight: 300;
  font-size: 3rem;
  color: white;
  display: block;
  height: 32px;
  width: 32px;
  line-height: 32px;
  margin-left: -16px;
  margin-top: -16px;
  text-align: center;
  opacity: 0;
  visibility: hidden;
  -webkit-transform: scale(0.5);
  transform: scale(0.5);
  -webkit-transition: all, 0.5s cubic-bezier(0.215, 0.61, 0.355, 1);
  transition: all, 0.5s cubic-bezier(0.215, 0.61, 0.355, 1);
  position: absolute;
  left: 50%;
  top: 50%;
}

.folio-item:hover .folio-item__thumb::before {
  opacity: 1;
  visibility: visible;
}

.folio-item:hover .folio-item__thumb::after {
  opacity: 1;
  visibility: visible;
  -webkit-transform: scale(1);
  transform: scale(1);
}

.folio-item-img {
  display: block;
  margin: 0;
  -webkit-transition: all, 0.5s;
  transition: all, 0.5s;
  background-size: cover;
}

.folio-item:hover .folio-item-img {
  -webkit-transform: scale(1.05);
  transform: scale(1.05);
}

