}

.contacto {
  background-color: #141914;
  margin: 0;
  padding: 5rem 0;
}
.contacto__heading {
  text-align: center;
  font-weight: 900;
  font-size: 4rem;
  font-family: "Andika", sans-serif;
  color: white;
}
.contacto__contenido {
  color: white;
  padding: 3rem;
  align-items: flex-start;
  justify-content: center;
  display: grid;
  gap: 4rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .contacto__contenido {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.contacto__texto {
  font-size: 3rem;
  text-align: center;
}
@media (min-width: 768px) {
  .contacto__texto {
    text-align: left;
  }
}
.contacto__info {
  color: #999999;
  font-size: 2.4rem;
}
.contacto__info--mod {
  color: white;
}
.contacto__info--mod-link {
  position: relative;
  padding: 1.4rem 4.2rem;
  padding-right: 3.1rem;
  font-size: 2.4rem;
  color: white;
  text-transform: uppercase;
  transition: all 500ms cubic-bezier(0.77, 0, 0.175, 1);
  cursor: pointer;
  user-select: none;
}
@media (min-width: 1024px) {
  .contacto__info--mod-link {
    position: relative;
    padding: 1.4rem 4.2rem;
    padding-right: 3.1rem;
    font-size: 2.4rem;
    color: white;
    letter-spacing: 1rem;
    text-transform: uppercase;
    transition: all 500ms cubic-bezier(0.77, 0, 0.175, 1);
    cursor: pointer;
    user-select: none;
  }
}
.contacto__info--mod-link::after, .contacto__info--mod-link::before {
  content: "";
  position: absolute;
  transition: inherit;
  z-index: 10;
}
.contacto__info--mod-link:hover {
  color: #141914;
  transition-delay: 0.5s;
  z-index: 100000;
}
.contacto__info--mod-link:hover:before {
  transition-delay: 0s;
}
.contacto__info--mod-link:hover:after {
  background-color: #d0d2d0;
  transition-delay: 0.35s;
  z-index: -1;
}
.contacto__info--mod-link::after, .contacto__info--mod-link::before {
  top: 0;
  width: 0;
  height: 100%;
}
.contacto__info--mod-link::before {
  left: 0;
  border: 1px solid white;
  border-left: 0;
  border-right: 0;
}
.contacto__info--mod-link::after {
  right: 0;
}
.contacto__info--mod-link:hover:before, .contacto__info--mod-link:hover:after {
  width: 100%;
}

.footer {
  background-color: #141914;
  margin: 0;
  border-top: 1px solid #505350;
}
.footer__texto {
  padding-top: 3rem;
  text-align: center;
  color: #505350;
  font-size: 1.6rem;
}
.footer__contenido {
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
  justify-content: center;
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .footer__contenido {
    flex-direction: row;
  }
}